      showContent(content) {
        this.$emit('contentChange', content)
      },
    }
  }
</script>

<style scoped>

  .aside-nav {
    grid-area: asidenav;

    background-color: #353538;
    border-radius: 10px;
  }

  @media screen and (max-width: 1000px) {
    .aside-nav__menu {

      display: flex;
      padding: 0;

      flex-direction: row; /* Изменяем направление на row для мобильных устройств */
      justify-content: space-around; /* Меняем распределение по оси главной */

      list-style-type: none;
    }
  }

  @media screen and (min-width: 1000px) {
    .aside-nav__menu {

      display: flex;
      padding: 0;

      flex-direction: column; /* Изменяем направление на row для мобильных устройств */
      gap: 20px;
      justify-content: space-around; /* Меняем распределение по оси главной */

      list-style-type: none;
    }
  }

  .aside-nav__menu a {

    display: flex;
    align-items: center;
    gap: 30px;

    fill: #DFB751;

    font-weight: 700;

    text-decoration: none;
  }

  .selected svg,
  .selected span {
    color: #ffffff;
    fill: #ffffff;
  }

  .genres, .charts, .favorites {
    padding: 8px 40px;
  }

  .aside-nav__menu li svg,
  .aside-nav__menu li span {
    transition: color 0.3s ease, fill 0.3s ease; /* Анимация изменения цвета */
  }

  .aside-nav__menu li:hover svg {
    fill: #ffffff; /* Новый цвет для заливки иконки SVG */
  }

  .aside-nav__menu li:hover span {
    color: #ffffff; /* Новый цвет для текста внутри <span> */
  }
</style>